/* start document page */
.document-square {
  height: 620px;
  border: 1px solid #666;
  position: relative;
  img {
    width: 100%;
    height: 100%;
  }
  .zoom-icons {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    color: #666;
    border-radius: 0.25rem 0 0 0.25rem;
    overflow: hidden;
    z-index: 1;
    i {
      width: 40px;
      height: 40px;
      line-height: 40px;
      text-align: center;
      cursor: pointer;
      font-size: 1.2rem;
      transition: 0.2s ease-in-out;
      background-color: #e0e0e0;

      &:hover {
        background-color: #f0f0f0;
      }
    }
  }
  .document-buttons {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 50%;
    left: -1rem;
    transform: translateY(-50%) translateX(-100%);

    button {
      margin: .5rem 0;
    }
  }
}
// .document-buttons {
//   display: flex;
//   justify-content: space-between;
//   button {
//     width: 32%;
//   }
// }

@media (max-width: 768px) {
  .document-square {
    margin-top: 60px;
    .document-buttons {
      flex-direction: row;
      position: absolute;
      top: -.5rem;
      left: 50%;
      transform: translateY(-100%) translateX(-50%);
      button {
        margin: 0 .5rem;
        min-width: min-content;
        white-space: nowrap;
      }
    }
  }
}

/* end document page */
