.report-container {
    position: relative;
}

.report-operations-icon {
    background: #eee;
    padding: 0.25rem;
    border-radius: 0.25rem;

    ul {
        list-style-type: none;
        display: flex;
        padding: 0;
        margin: 0;
        li {
            color: #444;
            padding: 0.75rem;
            font-size: 0.85rem;
            cursor: pointer;
            transition: 0.3s;
            border-radius: 0.25rem;
            &:hover {
                color: #311ae3;
                background: #e0e0e0;
            }
        }
    }
}

.report-btns {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 1rem;
    button {
        margin: 0 0.5rem;
        background-color: #fdceba;
        color: #1f45d1;
        display: inline-block;
        padding: 0.75rem 4rem;
        border-radius: 0.5rem;
        border: 1px solid #c3bcba;
    }
}

@media (max-width: 575px) {
    .report-btns {
        flex-direction: column;
        button {
            margin-bottom: 1rem;
        }
    }

}

@media (max-width: 768px) {
    .report-operations-icon {
        background: #eee;
        padding: 0.25rem;
        border-radius: 0.25rem;
    
        ul {
            flex-wrap: wrap;
            
            li {
                width: 33.33%;
            }
        }
    }
}
